body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: 18px;
	color: white;
}
.App {
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.Mainpage {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100%;
	width: 100%;
}
.Miniwrap {
	color: rgb(43, 29, 29);
	margin: 10em 0 0;
}
.Miniwrap h1 {
	text-align: center;
	font-size: 1.5rem;
}
.Miniwrap p {
	text-align: left;
	font-size: 1rem;
}
.Miniwrap .inputandbutton input {
	outline: none;
	width: 100%;

	border: 1px solid black;
	border-radius: 10%;
	border: none;
	padding: 5px;
	margin: 0rem;
	margin-right: -30px;
	font-size: 30px;
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.inputandbutton {
	display: flex;
	margin: 0 5em;
}
.inputandbutton button {
	border: none;
	font-size: 25px;
	margin-top: 7px;
	margin-right: 40px;
	width: 50px;
	height: 100%;
	display: inline-block;
	color: rgba(32, 3, 3, 0.712);
	background-color: rgb(113, 198, 255);
	margin-left: -20px;
	border-radius: 10%;
	cursor: pointer;
	z-index: 0;
}
.inputandbutton button:hover {
	animation: grow 1s;
	animation-fill-mode: forwards;
}
.Section1 {
	background-color: #c9bfac;
	width: 100%;
	height: 60vh;

	display: grid;
	grid-template-columns: 55% 45%;
	justify-content: left;

	flex-direction: column;
}
.Section1 h1 {
	font-size: 40px;
	padding-top: -20%;

	margin-top: -10px;
	margin-bottom: 50px;
}
.Miniwrap2 {
	margin: 0;
	height: 100%;
	width: 100%;
}
.Section1 p {
	text-align: center;
	font-size: 30px;
	font-family: 'Bree Serif', serif;
}
.Section2 {
	background-color: #ffa931;
	width: 100%;

	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 1.5em;
}
.Section2 > p {
	font-size: 40px;
	letter-spacing: 0.15em;
}
.Section2 img {
	margin-top: 35px;
}
.Section2 ol {
	margin: 1em 10% 0em;
	font-size: 1em;
	display: grid;
	grid-template-columns: auto 33% 33%;
}
.Section2 ol p {
	font-size: 0.75em;
}

.Section2 li {
	text-align: center;
	font-size: 1em;

	margin: 15px;
	list-style-position: inside;
	padding: 0;
}
.Section3 {
	background-color: #fecb89;
	width: 100%;
	height: 50vh;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	font-size: 2rem;
}
.Section3:hover {
	animation-name: animfadecolor1;
	animation-duration: 1s;
	animation-fill-mode: forwards;
}
.Section4:hover {
	animation-name: animfadecolor2;
	animation-duration: 1s;
	animation-fill-mode: forwards;
}
.Section4 {
	background-color: #fbe6d4;
	width: 100%;
	height: auto;
	display: flex;

	justify-content: center;
	color: darkgray;
	font-size: 25px;
}
.Section4 p {
	margin-left: -10%;
	text-align: left;
	font-size: 1.5em;
}

.Section4 p p {
	color: rgba(180, 64, 64, 0.993);
	padding: 30px;
	margin-left: 0%;
	font-size: 1.25em;
	font-family: 'Bree Serif', serif;
}
.Navbar {
	display: flex;
	justify-content: space-evenly;
	align-items: center;

	background-color: rgb(214, 255, 64);
	box-shadow: rgb(247, 54, 54);
	border: 4px solid rgb(224, 224, 224);
	width: 100%;
	height: 10vh;
}
a {
	border-radius: solid 10%;
	margin: 0 25vh;
	text-decoration: none;
}
.Navbar a:hover {
}

.Footer {
	display: flex;
	flex-direction: row;
	align-items: space-evenly;
	justify-content: space-evenly;
	width: 100%;
	background-color: rgb(221, 220, 226);
	margin-top: 1px;
}
.Footer div {
	border-radius: 1px;
	color: rgb(44, 44, 8);
	height: 5vh;
	margin: 25px 25px 0 25px;
}
@keyframes animfadecolor1 {
	from {
		background-color: #fecb89;
	}
	to {
		background-color: #fecb89c9;
	}
}

@keyframes animfadecolor2 {
	from {
		background-color: #fbe6d4;
	}
	to {
		background-color: rgb(145, 119, 90);
	}
}
@keyframes grow {
	from {
		margin-right: 40px;
		width: 50px;
	}
	to {
		margin-right: 30px;
		width: 100px;
	}
}
